const text = {
  first: 'This is me talking about HTML, CSS and Javascript, like the good learner I am.',
  second: 'This is me talking about PHP and Ruby and React more specifically, and all the cool animations etc.'
}

const pathOneIds = Object.keys(pathsOne);
const pathTwoIds = Object.keys(pathsTwo);
const pathThreeIds = Object.keys(pathsThree);

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1
  }).pipe(interpolate(from, to));

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.8 }
});

const IconOne = posed.path(
  pathOneIds.reduce((config, id) => {
    config[id] = {
      d: pathsOne[id],
      transition: morphTransition
    };

    return config;
  }, {})
);

const IconTwo = posed.path(
  pathTwoIds.reduce((config, id) => {
    config[id] = {
      d: pathsTwo[id],
      transition: morphTransition
    };

    return config;
  }, {})
);

const IconThree = posed.path(
  pathThreeIds.reduce((config, id) => {
    config[id] = {
      d: pathsThree[id],
      transition: morphTransition
    };

    return config;
  }, {})
);

const SVGText = posed.p({
  enter: { opacity: 1 },
  exit: { opacity: 0 }
});

