const viewbox = {
  HTML: '0 0 380 480',
  PHP: '0 0 640 480',
  Ruby: '0 0 560 520',
  React: '0 45 520 420'
}


class Skills extends Component {

  gotoNext = () => {
    const { pathIndexOne, pathIndexTwo, pathIndexThree } = this.state;
    const nextIndexOne = pathIndexOne + 1;
    const nextIndexTwo = pathIndexTwo + 1;
    const nextIndexThree = pathIndexThree + 1;
    this.setState({
      pathIndexOne: nextIndexOne > pathOneIds.length - 1 ? 0 : nextIndexOne,
      pathIndexTwo: nextIndexTwo > pathTwoIds.length - 1 ? 0 : nextIndexTwo,
      pathIndexThree: nextIndexThree > pathThreeIds.length - 1 ? 0 : nextIndexThree,
      textArray: this.state.textArray === 'first' ? 'second' : 'first',
      isVisibleOne : this.state.isVisibleOne === true ? false : true,
      isVisibleTwo : this.state.isVisibleTwo === true ? false : true,
      isCSS: this.state.isCSS === true ? false : true,
      isHTML: this.state.isHTML === true ? false : true,
      isJS: this.state.isJS === true ? false : true
    });
  };

  constructor(props){
    super();

    this.state = { 
      pathIndexOne: 0,
      pathIndexTwo: 0,
      pathIndexThree: 0,
      textArray: 'first',
      isVisibleOne: true,
      isVisibleTwo: false,
      isCSS: true,
      isHTML: true,
      isJS: true,
    }

  }

  render() {

      const { isVisibleOne } = this.state;
      const { isVisibleTwo } = this.state;
      const iscssSVG = this.state.isCSS === true ? 'cssSVG' : 'rubySVG';
      const isHTMLviewbox = this.state.isHTML === true ? viewbox.HTML : viewbox.PHP;
      const isphpSVG = this.state.isHTML === true ? null : 'phpSVG';
      const isCSSViewbox = this.state.isCSS === true ? '0 0 480 480' : viewbox.Ruby;
      const isjsSVG = this.state.isJS === true ? null : 'reactSVG';
      const isReactViewbox = this.state.isJS === true ? '0 0 480 480' : viewbox.React;

      return(
        <div className="col skills-section">
          
          <svg className={`svg first-svg ${isphpSVG}`} viewBox={isHTMLviewbox}>
            <IconOne pose={pathOneIds[this.state.pathIndexOne]} />
          </svg>

          <svg className={`svg second-svg ${isjsSVG}`} viewBox={isReactViewbox}>
            <IconTwo pose={pathTwoIds[this.state.pathIndexTwo]} />
          </svg>

          <svg className={`svg third-svg ${iscssSVG}`} viewBox={isCSSViewbox}>
            <IconThree pose={pathThreeIds[this.state.pathIndexThree]} />
          </svg>

          <PoseGroup>
            { isVisibleOne && [
                <SVGText key="SVGText" className="icon-text marg-upper index-one">{text['first']}</SVGText>
            ]}
          </PoseGroup>
          <PoseGroup>
            { isVisibleTwo && [
                <SVGText key="SVGText" className="icon-text marg-upper index-two">{text['second']}</SVGText>
            ]}
          </PoseGroup>

          <Fade delay={1000}>
            <NextButton className="button" onClick={this.gotoNext}><p className="icon-text button-text">Click me!</p></NextButton>
          </Fade>
        </div>
      )
  }
}

{/* 
    Okay, you most likely won't need to make a new Icon element,
    but you'll need to make a new path, new path ids,
    and then add them into state as well,  
    then also change state the same way for all of them on gotoNext. 
    That should do the trick. 
  */}

export default Skills;